body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-link {
  color: #61dafb;
}
.content{
  background-image: url(/lab11/static/media/Computer-Programmer.49444c92.jpg);
  background-size: cover;
  background-color: #34495ec5;
  background-blend-mode: multiply;
  height: 1000px;
}
.Main{
    font-size: 60px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    color: ivory;
    width: 1700px;
    text-align: center;
    padding-top: 200px;
}
img{
    width: 1000px;
}
section p{
    font-size: 40px;
    color: ivory;
    margin-bottom: 300px;
}
.Footer{
    position: absolute;
    bottom: -2100px;
    color: aliceblue;
    background-color: #161b20;
    height: 200px;
    width: 1905px;
}
.logo {
    position: absolute;
    left: 20px;
    width: 100px;
    pointer-events: painted;
}

.Header {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: baseline;
    font-size: 20px;
    width: 1000px;
    color: white;
    z-index: 1000;
}
.Nav{
    position: absolute;
    left: 300px;
    top: 30px;

}
a{
    color: ivory;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    margin: 80px;
    -webkit-text-decoration: wavy;
            text-decoration: wavy;
}
.About{
    padding: 10px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    background-color: ivory;
    color: black;
}
.About p{
    padding-left: 100px;
    font-size: 20px;
    color: black;
    text-align: left;
}
i{
    font-size: 24px;
    font-style: normal;
    color: gray;
    text-decoration: underline;
}
.ok{
    position: absolute;
    top: 700px;
    left: 500px;
    width: 300px;
}
.Products{
    float: left;
    background-image: url(/lab11/static/media/code.0bd308fe.png);
    background-blend-mode: multiply;
    background-color: #4f1c75;
    background-size: cover;
    background-attachment: fixed;
    padding: 100px 0;
    width: 100%;
    text-align: center;
    color: #fff;
}
img{
    width: 400px;
}
div .product {
    position: relative;
    background-color: rgba(0, 0, 0, 0.308);
    border-radius: 10px;
    height: 520px;
    width: 420px;
    margin: 90px;
    padding: 10px;
    display: inline-block;
    vertical-align: middle;
}
div .product:hover{
    background-color: rgba(104, 104, 104, 0.308);
}
.products{
    background-color: none;
    width: 1872px;
    height: 700px;
    display: flex;
    margin: 0px;
    padding-left: 30px;
}

p{
    font-size: large;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif
}
.modal {
    position: absolute;
    height: 800px;
    width: 1000px;
    border-radius: 20px;
    background-color: rgba(114, 113, 113, 0.938);
    z-index: 2;
    top: 1500px;
    left: 450px
}

button{
    border-radius: 10px;
    height: 40px;
    width: 80px;
    background-color: darkslategrey;
    color: whitesmoke;
    font-family: Arial, Helvetica, sans-serif;
}

.display-block {
    display: block;
}

.display-none {
    display: none;
}

.modal-img{
    position: absolute;
    left: 300px;
    top: 200px;
}
